body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.navbar{
    display: -ms-flexbox;
    display: flex;
    background-color: #fad000;
    padding: 15px 20px;
}
.menu-icon{
    margin-left: auto;
    background: #fff;
    border: none;
    padding: 15px 20px;
    cursor: pointer;
}

.produto{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 15px 20px;
}

.slide-fotos{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-right: 15px;
}
.slide-fotos img{
    padding: 5px;
    border: 1px solid #fad000;
    width: 50px;
    margin-top: 10px;
}

.info-produto{
    -ms-flex-positive: 2;
        flex-grow: 2;
    margin-left: 40px;
}

.foto-destaque{
    margin-left: 20px;
    padding: 10px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    margin-right: 50px;
    text-align: center;
    border: 1px solid #aaa;
    -ms-flex: 0 0 20%;
        flex: 0 0 20%;
}
.foto-destaque img{
    max-height: 200px;
    
}

.carrinho{
    background-color: #be9f00;
    max-height: 400px;
    overflow: auto;
}
.item-carrinho{
    padding: 15px 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.image-container{
    width: 100px;
    height: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 20px;
}
.img-item{
    display: block;
    max-width: 100%;
    height: auto;
}
.item-carrinho h1, .item-carrinho h2{
    margin-top: 0;
    margin-bottom: 0;
}

.nome-item{
    color: #fff;
    font-size: 1.2em;
    margin-bottom: 10px !important;
}
.parcelado, .avista{
    font-size: 1em;
    font-weight: 600;
    color: #444;
}

.closebtn{
    margin-left: auto;
}

.subtotal{
    padding: 15px 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: end;
        align-items: flex-end;
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: #fad000
}
.subtotal h2{
    padding-top: 10px;
}
.subtotal h2, .subtotal h3{
    margin: 0;
}

hr{
    margin-top: 0;
    padding-top: 0;
    border: none;
    border-top: 1px solid #333;
    width: 100%;
    margin-bottom: 20px;
}
.hide{
    -webkit-transition: 500ms max-height ease-in 0ms;
    -o-transition: 500ms max-height ease-in 0ms;
    transition: 500ms max-height ease-in 0ms;
    max-height: 0 !important;
    overflow: hidden !important;
}
.wrapperEffect{
    -webkit-transition: 600ms max-height ease-in;
    -o-transition: 600ms max-height ease-in;
    transition: 600ms max-height ease-in;
    max-height: 500px;
    overflow: auto;
}

.tag{
    background: #3498db;
    padding: 10px 15px;
    color: #fff;
}
.compras{
    margin-top: 20px;
}

.addcarrinho{
    background: #2ecc71;
    border: none;
    padding: 20px;
    color: #fff;
    cursor: pointer;
}

.addcarrinho:hover{
    background: #27ae60;
    -webkit-transition: background 200ms ease-in;
    -o-transition: background 200ms ease-in;
    transition: background 200ms ease-in;
}


